import React from 'react'
import styles from './UserMenu.module.css'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

export default function UserMenu({display , user}) {

    return (
        <div className={styles.container}>
            <div className={styles.loginContainer}>
                <div className={styles.loginHeader}>
                    <div className={styles.loginIcon}
                         onClick={display}>
                        <FontAwesomeIcon icon={faTimes}/>
                    </div>
                    <div className={styles.loginTextHeader}>
                        fungAraiDee
                    </div>
                </div>
                <div className={styles.loginContent}>
                    <div className={styles.loginContentText}>
                        เลือก Service ที่ต้องการเข้าสู่ระบบ
                    </div>

                    <div className={styles.btnLogin} style={{marginTop:15}}>
                        <div className={styles.btnLoginText}>โปรไฟล์ @{user.displayName.split(" ")[0]}</div>
                    </div>

                    <div className={styles.btnLogin} style={{marginTop:15}}>
                        <div className={styles.btnLoginText}>ออกจากระบบ @{user.displayName.split(" ")[0]}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}
